import { defaultProps, defaultOptions } from '../common/component-handler/press-component';

import { range } from '../common/format/number';
import { touch } from '../mixins/basic/touch';


const THRESHOLD = 0.3;

let ARRAY = [];

export default {
  name: 'PressSwipeCell',
  options: { ...defaultOptions },
  mixins: [touch],
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    leftWidth: {
      type: Number,
      default: 0,
    },
    rightWidth: {
      type: Number,
      default: 0,
    },
    asyncClose: {
      type: Boolean,
      default: false,
    },
    name: {
      type: String,
      default: '',
    },
    ...defaultProps,
  },
  emits: ['click', 'open', 'close'],
  data() {
    return {
      catchMove: false,
      wrapperStyle: '',
      offset: 0,
    };
  },
  watch: {
    leftWidth: {
      handler(leftWidth = 0) {
        if (this.offset > 0) {
          this.swipeMove(leftWidth);
        }
      },
    },
    rightWidth: {
      handler(rightWidth = 0) {
        if (this.offset < 0) {
          this.swipeMove(-rightWidth);
        }
      },
    },
  },
  created() {
    this.offset = 0;
    ARRAY.push(this);
  },
  destroyed() {
    ARRAY = ARRAY.filter(item => item !== this);
  },
  methods: {
    setData(data) {
      Object.keys(data).forEach((key) => {
        this[key] = data[key];
      });
    },
    open(position) {
      const { leftWidth, rightWidth } = this;
      const offset = position === 'left' ? leftWidth : -rightWidth;
      this.swipeMove(offset);
      this.$emit('open', {
        position,
        name: this.name,
      });
    },
    close() {
      this.swipeMove(0);
    },
    swipeMove(offset = 0) {
      this.offset = range(offset, -this.rightWidth, this.leftWidth);
      const transform = `translate3d(${this.offset}px, 0, 0)`;
      const transition = this.dragging
        ? 'none'
        : 'transform .6s cubic-bezier(0.18, 0.89, 0.32, 1)';
      this.setData({
        wrapperStyle: `
        -webkit-transform: ${transform};
        -webkit-transition: ${transition};
        transform: ${transform};
        transition: ${transition};
      `,
      });
    },
    swipeLeaveTransition() {
      const { leftWidth, rightWidth } = this;
      const { offset } = this;
      if (rightWidth > 0 && -offset > rightWidth * THRESHOLD) {
        this.open('right');
      } else if (leftWidth > 0 && offset > leftWidth * THRESHOLD) {
        this.open('left');
      } else {
        this.swipeMove(0);
      }
      this.setData({ catchMove: false });
    },
    startDrag(event) {
      if (this.disabled) {
        return;
      }
      this.startOffset = this.offset;
      this.touchStart(event);
    },
    noop() { },
    onDrag(event) {
      // if (this.catchMove) {
      //   event.stopPropagation();
      //   event.preventDefault();
      //   return;
      // }
      if (this.disabled) {
        return;
      }
      this.touchMove(event);
      if (this.direction !== 'horizontal') {
        return;
      }
      this.dragging = true;
      ARRAY.filter(item => item !== this && item.offset !== 0).forEach(item => item.close());
      this.setData({ catchMove: true });
      this.swipeMove(this.startOffset + this.deltaX);
    },
    endDrag() {
      if (this.disabled) {
        return;
      }
      this.dragging = false;
      this.swipeLeaveTransition();
    },
    onClick(event) {
      const { key: position = 'outside' } = event.currentTarget.dataset;
      this.$emit('click', position);
      if (!this.offset) {
        return;
      }
      if (this.asyncClose) {
        this.$emit('close', {
          position,
          name: this.name,

          instance: this,

        });
      } else {
        this.swipeMove(0);
      }
    },
  },
};
